import React, { Component } from 'react';
import '../css/Clicker.css';

class Clicker extends Component {
  constructor() {
    super();
    this.generatePosition = this.generatePosition.bind(this);
    this.moveAndUpdateScore = this.moveAndUpdateScore.bind(this);
  }
  
  componentDidMount() {
    this.generatePosition();
  }
  
  generatePosition() {
    if(this.props.availableArea){
      const area = this.props.availableArea.getClientRects()[0];
      let bottom = area.bottom - 60;
      let right = area.right - 150;
      this.x = Math.floor(Math.random()*(bottom-area.top+1)+area.top);
      this.y = Math.floor(Math.random()*(right-area.left+1)+area.left);
    }
  }
  
  moveAndUpdateScore() {
    if(this.props.winner){
      this.props.increaseScore();
    }
    this.generatePosition();
  }
  
  render() {
    return (
      <div className="clicker" style={{
        left: this.y + 'px',
        top: this.x + 'px'
      }} onClick={this.moveAndUpdateScore} >Clicker!</div>
    )
  }
}

export default Clicker;



