import React, { Component } from 'react';
import '../css/Logo.css';

class Logo extends Component {
  render() {
    return (
      <div className="Logo">
        <h1>
          <span className="body">{this.props.title}</span>
          <span className="red">I</span>
          <span className="green">I</span>
          <span className="blue">I</span>
        </h1>
      </div>
    );
  }
}

export default Logo;



