import React, { Component } from 'react';
import Clicker from './Clicker';

class Game extends Component {
  
  render() {
    return (
      <div className="clickerArea" ref={(clicker) => this.clickerArea = clicker}>
        <Clicker winner="true" increaseScore={this.props.increaseScore} availableArea={this.clickerArea}/>
      
      </div>
    )
  }
}

export default Game;



